
.container {
    text-align: center;
    
}

.button1 {
    position: relative;
    display: inline-block;
    background-color: red;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: .3em;
    border: solid .5em #000000;
    font-size: 3em;
    animation: moving 5s linear infinite;
}
@keyframes moving{
    0%{
        top:0;
        left:0;
    }
    25%{
        top: 200px;
        left: 0;
        background:cyan;
    }
    50%{
        top: 0;
        left: 0;
    }
    75%{
        top: 200px;
    }
    100%{
        top: 0;
    }
}

